:root {

	/* Z-index levels */
}

.root {}

.main {
	position: fixed;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	background: #fff;
	width: 450px;
	overflow: hidden;
	outline: none;
	max-width: calc(100% - 30px);
	z-index: calc(400 + 1);
}

.title {
	position: relative;
}

.open {

}

.close {
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	right: 35px;
	cursor: pointer;
	font-size: 0;
	border: none;
	background: none;
}

.close i {
	font-size: 14px;
}

.content {}

.underlayVisible {
	background: rgba(66, 66, 66, 0.3);
	z-index: 400;
}

@-webkit-keyframes fadeIn {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

@keyframes fadeIn {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

.enter,
.underlayEnter {
	-webkit-animation-duration: .2s;
	        animation-duration: .2s;
	-webkit-animation-fill-mode: both;
	        animation-fill-mode: both;
	-webkit-animation-name: fadeIn;
	        animation-name: fadeIn;
}

@-webkit-keyframes fadeOut {
	from {
		opacity: 1;
	}
	to {
		opacity: 0;
	}
}

@keyframes fadeOut {
	from {
		opacity: 1;
	}
	to {
		opacity: 0;
	}
}

.exit,
.underlayExit {
	-webkit-animation-duration: .2s;
	        animation-duration: .2s;
	-webkit-animation-fill-mode: both;
	        animation-fill-mode: both;
	-webkit-animation-name: fadeOut;
	        animation-name: fadeOut;
}
